import { MsgUpdateParams, MsgCreateCertificate } from "./tx";
export const registry = [["/empowerchain.certificates.MsgUpdateParams", MsgUpdateParams], ["/empowerchain.certificates.MsgCreateCertificate", MsgCreateCertificate]];
export const load = protoRegistry => {
  registry.forEach(([typeUrl, mod]) => {
    protoRegistry.register(typeUrl, mod);
  });
};
export const MessageComposer = {
  encoded: {
    updateParams(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgUpdateParams",
        value: MsgUpdateParams.encode(value).finish()
      };
    },
    createCertificate(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgCreateCertificate",
        value: MsgCreateCertificate.encode(value).finish()
      };
    }
  },
  withTypeUrl: {
    updateParams(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgUpdateParams",
        value
      };
    },
    createCertificate(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgCreateCertificate",
        value
      };
    }
  },
  toJSON: {
    updateParams(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgUpdateParams",
        value: MsgUpdateParams.toJSON(value)
      };
    },
    createCertificate(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgCreateCertificate",
        value: MsgCreateCertificate.toJSON(value)
      };
    }
  },
  fromJSON: {
    updateParams(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgUpdateParams",
        value: MsgUpdateParams.fromJSON(value)
      };
    },
    createCertificate(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgCreateCertificate",
        value: MsgCreateCertificate.fromJSON(value)
      };
    }
  },
  fromPartial: {
    updateParams(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgUpdateParams",
        value: MsgUpdateParams.fromPartial(value)
      };
    },
    createCertificate(value) {
      return {
        typeUrl: "/empowerchain.certificates.MsgCreateCertificate",
        value: MsgCreateCertificate.fromPartial(value)
      };
    }
  }
};